/* 棋盘格子的样式 */
.square {
    background-color: #ddd;
    border: 1px solid #999;
    font-size: 24px;
    line-height: 34px;
    height: 34px;
    text-align: center;
    width: 34px;
}

/* X 和 O 棋子的样式 */
.X{
    color: black;
}

.o {
    color: white;
}


/* 其他样式保持不变 */
.status {
    margin-bottom: 10px;
}

.game-info {
    margin-top: 10px;
}

.board-row:after {
    clear: both;
    content: "";
    display: table;
}

/* 修改 game 类以使按钮和历史记录放在最左边，棋盘放中间 */
.game {
    display: flex;
    justify-content: flex-start; /* 从左开始排列 */
    align-items: flex-start; /* 顶部对齐 */
}

/* 按钮容器的样式 */
.buttons-container {
    /* 设置固定宽度或根据内容自适应 */
    width: 150px; /* 假设按钮容器宽度为150px */
}

/* 棋盘容器的样式 */
.board-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* 棋盘居中显示 */
    margin: 0 auto; /* 添加自动外边距使棋盘居中 */
}

/* 历史记录侧边栏的样式 */
.history {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    /* 历史记录靠左对齐 */
    width: 150px; /* 假设历史记录侧边栏宽度为150px */
    margin-right: 20px; /* 添加右边距以与棋盘分隔 */
}

/* 历史记录信息样式 */
.history-info {
    text-align: left; /* 历史记录文本左对齐 */
}
